import React from "react";
import { FaTrash, FaCopy } from "react-icons/fa";
const List = ({ items, remove, copy }) => {
  return (
    <div className="todo-list">
      {items.map((item) => {
        const { id, title } = item;
        return (
          <article className="todo-item" key={id}>
            <p className="title">{title}</p>
            <div className="btn-container">
              <button
                type="button"
                className="copy-btn"
                onClick={() => {
                  copy(title);
                }}
              >
                <FaCopy />
              </button>
              <button
                type="button"
                className="delete-btn"
                onClick={() => remove(id)}
              >
                <FaTrash />
              </button>
            </div>
          </article>
        );
      })}
    </div>
  );
};

export default List;
