import React, { useState } from "react";
import List from "./List";
import Alert from "./Alert";

let storedList;
storedList = localStorage.getItem("list")
  ? localStorage
      .getItem("list")
      .split(";")
      .map((i) => {
        const item = i.split("+");
        return { id: item[0].substr(3), title: item[1].substr(6) };
      })
  : [];

function App() {
  const [alert, setAlert] = useState({
    show: false,
    msg: "",
    type: "",
  });
  const [input, setInput] = useState("");
  const [list, setList] = useState(storedList);
  const [isEmpty, setIsEmpty] = useState(true);
  const handleSubmit = (e) => {
    e.preventDefault();
    if (!input) {
      showAlert(true, "unsupported text type", "danger");
    } else {
      showAlert(true, "item added", "success");
      const newItem = { id: new Date().getTime().toString(), title: input };
      setList([...list, newItem]);
      setInput("");
    }
  };
  const showAlert = (show = false, msg = "", type = "") => {
    setAlert({ show, msg, type });
  };
  const removeTodoItem = (id) => {
    console.log(4);
    showAlert(true, "item removed", "danger");
    const newList = list.filter((item) => item.id !== id);
    setList(newList);
  };
  const copyItem = (item) => {
    showAlert(true, "item sent to clipboard!", "success");
    navigator.clipboard.writeText(item);
  };
  const format = () => {
    setList([]);
    showAlert(true, "Congrats!", "success");
  };
  React.useEffect(() => {
    const listToSave = list.map((i) => {
      return "id:" + i.id + "+title:" + i.title;
    });
    list.length === 0 ? setIsEmpty(true) : setIsEmpty(false);
    localStorage.setItem("list", listToSave.join(";"));
  }, [list]);
  return (
    <>
      <section className="section-center">
        {alert.show && <Alert alert={alert} remove={showAlert} />}
        <form action="" className="todo-form" onSubmit={handleSubmit}>
          <h3>Todo List</h3>
          <div className="form-control">
            <input
              type="text"
              className="todo"
              onChange={(e) => setInput(e.target.value)}
              value={input}
              placeholder="Read a book"
            />
            <button type="submit" className="submit-btn">
              Submit
            </button>
          </div>
        </form>
        <div className="todo-container">
          <List items={list} remove={removeTodoItem} copy={copyItem} />
          {isEmpty ? (
            <p className="text-center">Nothing but crickets</p>
          ) : (
            <button className="clear-btn" onClick={format}>
              Clear All
            </button>
          )}
        </div>
      </section>
    </>
  );
}

export default App;
