import React from "react";

const Alert = ({ alert, remove }) => {
  const { msg, type } = alert;
  React.useEffect(() => {
    const t = setTimeout(() => {
      remove();
    }, 2000);
    return () => clearTimeout(t);
  });
  return (
    <div className="alert-box">
      <p className={`alert-text alert-${type}`}>{msg}</p>
    </div>
  );
};

export default Alert;
